import { Model, MODEL } from "./model";
import * as L from "leaflet";


import "leaflet/dist/leaflet.css";
import "./index.css";

const designMode = window.location.search.includes("design");

const map = L.map("map")
  .fitWorld()
  .on("locationfound", onLocationFound)
  .on("locationerror", (e) => alert(e.message))
  .on("click", (e: L.LeafletMouseEvent) => console.log(e.latlng))
  .locate({ setView: true, maxZoom: 16 });

const baseLayers = {
  Kort: L.tileLayer("//{s}.tile.openstreetmap.org/{z}/{x}/{y}.png", {
    attribution:
      '&copy; <a href="http://www.openstreetmap.org/copyright">OpenStreetMap</a>',
  }).addTo(map),
  Satellit: L.tileLayer(
    "//server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}",
    {
      attribution: '&copy; <a href="http://www.esri.com">Esri</a>',
    }
  ),
  Kort2: L.tileLayer(
    "https://api.mapbox.com/styles/v1/{id}/tiles/{z}/{x}/{y}?access_token=pk.eyJ1IjoibWFwYm94IiwiYSI6ImNpejY4NXVycTA2emYycXBndHRqcmZ3N3gifQ.rJcFIG214AriISLbB6B5aw",
    {
      maxZoom: 18,
      attribution:
        'Map data &copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors, ' +
        'Imagery © <a href="https://www.mapbox.com/">Mapbox</a>',
      id: "mapbox/streets-v11",
      tileSize: 512,
      zoomOffset: -1,
    }
  ),
};

L.control.layers(baseLayers).addTo(map);

function onLocationFound(e) {
  const radius = e.accuracy / 2;

  const locationMarker = L.marker(e.latlng)
    .addTo(map)
    .bindPopup("You are within " + radius + " meters from this point")
    .openPopup();

  const locationCircle = L.circle(e.latlng, radius).addTo(map);
}

class Wire {
  name?: string;
  ends: Endpoint[];
  private line: L.Polyline;

  constructor(ends: Endpoint[]) {
    this.ends = ends;

    this.line = L.polyline(
      ends.map((x) => x.getNode().getLatLng()),
      { color: "red" }
    ).addTo(map);
  }

  repositionLine() {
    this.line.setLatLngs(this.ends.map((x) => x.getNode().getLatLng()));
  }

  getOtherEnds(end: Endpoint) {
    return this.ends.filter((x) => x !== end);
  }

  setSupplied(isSupplied: boolean) {
    this.line.setStyle({ color: isSupplied ? "red" : "black" });
  }
}

interface Node {
  get name(): string;
  getLatLng(): L.LatLng;
  getLocalNext(localSupply: Endpoint): Endpoint[];
}

interface Endpoint {
  getNode(): Node;
}

class Supply implements Node, Endpoint {
  name: string;
  pos: L.LatLng;
  enabled: boolean;
  private marker: L.Marker;
  constructor(
    name: string,
    init: { pos: { lat: number; lng: number }; enabled: boolean }
  ) {
    this.name = name;
    this.pos = L.latLng(init.pos);
    this.enabled = init.enabled;

    this.marker = L.marker(this.pos, {
      title: this.name,
      draggable: designMode,
    })
      .on("click", (e) => {
        this.enabled = !this.enabled;
        refresh();
      })
      .on("dragend", (e) => {
        this.pos = this.marker.getLatLng();
        const wire = getWire(this);
        wire.repositionLine();
        dumpModel();
      })
      .addTo(map);
  }

  getLatLng(): L.LatLng {
    return this.pos;
  }

  getLocalNext(localSupply: Endpoint): Endpoint[] {
    return [this];
  }

  getNode(): Node {
    return this;
  }
}

class SwitchTerminal implements Endpoint {
  constructor(private s: Switch) {}

  getNode(): Node {
    return this.s;
  }
}

