class Switch implements Node {
  name: string;
  pos: L.LatLng;
  open: boolean;
  defaultOpen: boolean;
  terminals: SwitchTerminal[] = [];
  private marker: L.Marker;

  constructor(
    name: string,
    init: { pos: { lat: number; lng: number }; open?: boolean }
  ) {
    this.name = name;
    this.pos = L.latLng(init.pos);
    this.defaultOpen = init.open ?? false;
    this.open = this.defaultOpen;

    this.marker = L.marker(this.pos, {
      icon: this.open ? toggleOffIcon : toggleOnIcon,
      draggable: designMode,
    })
      .setTooltipContent(this.name)
      .on("click", (e) => {
        this.open = !this.open;
        this.marker.setIcon(this.open ? toggleOffIcon : toggleOnIcon);
        refresh();
      })
      .on("dragend", (e) => {
        this.pos = this.marker.getLatLng();
        for (const terminal of this.terminals) {
          const wire = getWire(terminal);
          wire.repositionLine();
        }
        dumpModel();
      })
      .addTo(map);
  }

  getNextTerminal() {
    if (this.terminals.length === 2) {
      throw new Error(
        "Invalid operations - at most two terminals are supported for a switch"
      );
    }
    const terminal = new SwitchTerminal(this);
    this.terminals.push(terminal);
    return terminal;
  }

  getLatLng(): L.LatLng {
    return this.pos;
  }

  getLocalNext(localSupply: Endpoint): Endpoint[] {
    if (this.open || !(localSupply instanceof SwitchTerminal)) {
      return [];
    }

    const index = this.terminals.indexOf(localSupply);
    if (index === -1) {
      return [];
    }

    return this.terminals.filter((x, i) => i !== index);
  }
}

const supplies = Object.fromEntries(
  Object.getOwnPropertyNames(MODEL.supplies).map((name) => [
    name,
    new Supply(name, MODEL.supplies[name]),
  ])
);
const switches = Object.fromEntries(
  Object.getOwnPropertyNames(MODEL.switches).map((name) => [
    name,
    new Switch(name, MODEL.switches[name]),
  ])
);
const wires = MODEL.wires.map(
  (init) => new Wire(init.map((end) => getWireEndById(end)))
);

function getWireEndById(id: string): Endpoint {
  const supply = supplies[id];
  if (supply) {
    return supply;
  }
  const s = switches[id];

  if (!s) {
    throw new Error(`Supply or switch with id '${id}' was not found`);
  }
  return s.getNextTerminal();
}

function getWire(terminal: Endpoint) {
  for (const wire of wires) {
    if (wire.ends.includes(terminal)) {
      return wire;
    }
  }

  throw new Error("Wire not found");
}

function dumpModel() {
  const model: Model = {
    supplies: {},
    switches: {},
    wires: [],
  };

  for (const id of Object.keys(supplies)) {
    const supply = supplies[id];
    model.supplies[id] = { pos: supply.pos, enabled: supply.enabled };
  }

  for (const id of Object.keys(switches)) {
    const sw = switches[id];
    model.switches[id] = {
      pos: sw.pos,
      ...(sw.open && { open: sw.open }),
    };
  }

  for (const wire of wires) {
    model.wires.push(wire.ends.map((x) => x.getNode().name));
  }

  console.log(JSON.stringify(model));
}

function visit(localSupply: Endpoint, supplied: Set<Endpoint>) {
  supplied.add(localSupply);
  for (const localNext of localSupply.getNode().getLocalNext(localSupply)) {
    supplied.add(localNext);
    const wire = getWire(localNext);
    for (const end of wire.getOtherEnds(localNext)) {
      if (supplied.has(end)) {
        continue;
      }
      visit(end, supplied);
    }
  }
}

function refresh() {
  const supplied = new Set<Endpoint>();

  for (const supply of Object.values(supplies).filter((x) => x.enabled)) {
    visit(supply, supplied);
  }

  for (const wire of wires) {
    const isSupplied = !wire.ends.some((terminal) => !supplied.has(terminal));
    wire.setSupplied(isSupplied);
  }
}

refresh();
